import { Descriptions, Modal } from 'antd';
import EmptyPlaceholder from 'components/EmptyPlaceholder/EmptyPlaceholder';
import NullPlaceholder from 'components/NullPlaceholder/NullPlaceholder';
import React, { useState } from 'react';

const { Item } = Descriptions;

const EmrDesc = props => {
  const { emr, role } = props;
  const {
    medical_record: {
      administrative: {
        fullname,
        dayOfBirth,
        gender,
        job,
        ethnicity,
        expatriate,
        address,
        workplace,
        object,
        insurance_expirity,
        insurance_number,
        family_member_name,
        family_member_address,
        phone,
        checkin_at,
        previous_diagnose,
        come_from
      },
      present_complaint,
      ask: {
        pathological_process,
        self_medical_history,
        family_medical_history
      },
      examination: {
        heartbeat,
        temperature,
        blood_pressure,
        breathing,
        weight,
        body,
        partials,
        subclinical_summary,
        initial_diagnose,
        drugs,
        processed,
        diagnose,
        from_date,
        to_date
      },
      summary: {
        pathological_process_and_clinical_course,
        valuable_subclinical_summary,
        primary_disease,
        sub_disease,
        treatment_method,
        patient_status,
        direction_of_treatment,
        services,
        attachments
      }
    }
  } = emr;
  const [selectedImage, setSelectedImage] = useState('');
  const [showModal, setShowModal] = useState(false);

  function handleCloseModal() {
    setSelectedImage('');
    setShowModal(false);
  }

  function handleImageClick(url) {
    setSelectedImage(url);
    setShowModal(true);
  }

  return (
    <div>
      <div className="mb-2">Phng khm: {emr.room.name}</div>
      <div className="mb-2">Cp nht bi: bc s <strong>{emr.physician.first_name} {emr.physician.last_name}</strong></div>
      <Descriptions title="I. Hnh chnh" size="middle" bordered column={2} className="mb-5">
        <Item label="1. H v tn">{fullname || NullPlaceholder}</Item>
        <Item label="2. Ngy sinh">{dayOfBirth || NullPlaceholder}</Item>
        <Item label="3. Gii tnh">{gender || NullPlaceholder}</Item>
        <Item label="4. Ngh nghip">{job || NullPlaceholder}</Item>
        <Item label="5. Dn tc">{ethnicity || NullPlaceholder}</Item>
        <Item label="6. Ngoi kiu">{expatriate || NullPlaceholder}</Item>
        <Item label="7. a ch" span={2}>{address || NullPlaceholder}</Item>
        <Item label="8. Ni lm vic">{workplace || NullPlaceholder}</Item>
        <Item label="9. i tng">{object || NullPlaceholder}</Item>
        <Item label="10. BHYT gi tr n ngy">{insurance_expirity || NullPlaceholder}</Item>
        <Item label="S th BHYT">{insurance_number || NullPlaceholder}</Item>
        <Item label="11. H tn, a ch ngi nh khi cn bo tin" span={2}>{family_member_name && family_member_address ? `${family_member_name}, ${family_member_address}` : NullPlaceholder}</Item>
        <Item label="S in thoi">{phone || NullPlaceholder}</Item>
        <Item label="12. n khm bnh lc">{checkin_at || NullPlaceholder}</Item>
        <Item label="13. Chn on ca ni gii thiu">{previous_diagnose && come_from ? `${previous_diagnose} (${come_from})` : NullPlaceholder}</Item>
      </Descriptions>
      {
        role !== "patient" &&
        <>
          <Descriptions title="II. L do vo vin" bordered size="middle" className="mb-5">
            <Item label="L do vo vin">{present_complaint || EmptyPlaceholder}</Item>
          </Descriptions>

          <Descriptions title="III. Hi bnh" bordered layout="vertical" column={2} className="mb-5">
            <Item label="1. Qu trnh bnh l" span={2}>
              <div dangerouslySetInnerHTML={{ __html: pathological_process || "Cha cp nht" }} className="whitespace-pre-line"></div>
            </Item>
            <Item label="2. Tin s bnh bn thn">{self_medical_history || "Cha cp nht"}</Item>
            <Item label="Gia nh">{family_medical_history || "Cha cp nht"}</Item>
          </Descriptions>

          <Descriptions title="IV. Khm bnh" bordered column={2} layout="vertical" className="mb-5">
            <Item label="1. Ton thn" className="align-baseline">
              <div dangerouslySetInnerHTML={{ __html: body || "Cha cp nht" }} className="whitespace-pre-line"></div>
            </Item>
            <Item label="Du hiu sinh tn">
              <div>Mch: {heartbeat || EmptyPlaceholder} <i className="ml-auto">ln/ph</i></div>
              <div>Nhit : {temperature || EmptyPlaceholder} <i className="ml-auto">C</i></div>
              <div>Huyt p: {blood_pressure || EmptyPlaceholder} <i className="ml-auto">mmHg</i></div>
              <div>Nhp th: {breathing || EmptyPlaceholder} <i className="ml-auto">ln/ph</i></div>
              <div>Cn nng: {weight || EmptyPlaceholder} <i className="ml-auto">kg</i></div>
            </Item>
            <Item label="2. Cc b phn" span={2} className="align-baseline">
              <div dangerouslySetInnerHTML={{ __html: partials || "Cha cp nht" }} className="whitespace-pre-line"></div>
            </Item>
            <Item label="3. Tm tt kt qu cn lm sng" span={2}>
              <div dangerouslySetInnerHTML={{ __html: subclinical_summary || "Cha cp nht" }} className="whitespace-pre-line"></div>
            </Item>
            <Item label="4. Chn on ban u" span={2}>{initial_diagnose?.disease || NullPlaceholder}</Item>
            <Item label="5. Thuc">
              {
                drugs?.map((d, index) => {
                  return <div key={index}>{d.drug} x {d.total} ({d.drugInstruction})</div>
                }) || NullPlaceholder
              }
            </Item>
            <Item label=" x l">
              <div dangerouslySetInnerHTML={{ __html: processed || "Cha cp nht" }} className="whitespace-pre-line"></div>
            </Item>
            <Item label="6. Chn on khi ra vin" span={2}>{diagnose?.disease || NullPlaceholder}</Item>
            <Item label="7. iu tr ngoi tr">{from_date && to_date ? `T ngy ${from_date} n ngy ${to_date}` : NullPlaceholder}</Item>
          </Descriptions>
        </>
      }

      <Descriptions title="Tng kt bnh n" bordered layout="vertical" column={2}>
        <Item label="1. Qu trnh bnh l v din bin lm sng" span={2}>
          <div dangerouslySetInnerHTML={{ __html: pathological_process_and_clinical_course || "Cha cp nht" }} className="whitespace-pre-line"></div>
        </Item>
        <Item label="2. Tm tt kt qu xt nghim cn lm sng c gi tr chn on" span={2}>
          <div dangerouslySetInnerHTML={{ __html: valuable_subclinical_summary || "Cha cp nht" }} className="whitespace-pre-line"></div>
        </Item>
        <Item label="3. Chn on ra vin" span={2}>
          <div>Bnh chnh: {primary_disease?.disease || "Khng"}</div>
          <div>Bnh km theo (nu c): {sub_disease?.disease || "Khng"}</div>
        </Item>
        <Item label="4. Phng php iu tr" span={2}>
          <div dangerouslySetInnerHTML={{ __html: treatment_method || "Cha cp nht" }} className="whitespace-pre-line"></div>
        </Item>
        <Item label="5. Tnh trng ngi bnh ra vin">
          <div dangerouslySetInnerHTML={{ __html: patient_status || "Cha cp nht" }} className="whitespace-pre-line"></div>
        </Item>
        <Item label="6. Hng iu tr v cc ch  tip theo">
          <div dangerouslySetInnerHTML={{ __html: direction_of_treatment || "Cha cp nht" }} className="whitespace-pre-line"></div>
        </Item>
        {
          role !== "patient" &&
          <Item label="Dch v" className="align-baseline">
            {
              services?.map((s, i) => (
                <div key={i}>{i + 1}. {s.name || s.id}</div>
              )) || EmptyPlaceholder
            }
          </Item>
        }
        {
          role !== "patient" &&
          <Item label="Tp nh km">
            {
              attachments && attachments.length > 0 && attachments.map((a, i) => (
                <img src={a.url} key={i} style={{ width: 200 }} onClick={() => handleImageClick(a.url)} />
              )) || EmptyPlaceholder
            }
          </Item>
        }
      </Descriptions>

      <Modal
        visible={showModal}
        title="Hnh nh"
        onOk={handleCloseModal}
        onCancel={handleCloseModal}>
        <img src={selectedImage} style={{ width: '100%' }} />
      </Modal>
    </div>
  )
}

export default EmrDesc;